<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  El-UI -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getOrdersList"
            @keyup.enter.native="handleInputConfirm()"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="showTips"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.order_pay === '0'"
              ></el-tag
            >
            <el-tag type="" v-if="scope.row.order_pay === '1'"></el-tag>
            <el-tag type="success" v-if="scope.row.order_pay === '2'"
              ></el-tag
            >
            <el-tag type="warning" v-if="scope.row.order_pay === '3'"
              ></el-tag
            >
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.is_send === ''"></el-tag>
            <el-tag type="success" v-if="scope.row.is_send === ''"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="create_time">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="175px">
          <!-- slot-scope="scope" -->
          <template>
            <!--   -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
              :open-delay="1000"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditGoodsAddressDialog"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
              :open-delay="1000"
            >
              <el-button
                type="success"
                icon="el-icon-truck"
                size="mini"
                @click="showLogisticsStatusBox"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10, 20, 50]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editAddressDialogVisible"
      width="40%"
      @close="editAddressDialogClose"
    >
      <!--  -->
      <span>
        <el-form
          :model="editAddressrForm"
          :rules="editAddressrFormRules"
          ref="editAddressrFormRef"
          label-width="100px"
        >
          <el-form-item label="/" prop="address1">
            <el-cascader
              :options="cityData"
              clearable
              :props="{ expandTrigger: 'hover' }"
              v-model="editAddressrForm.address1"
            ></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address2">
            <el-input v-model="editAddressrForm.address2"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editAddressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitEditAddress"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="logisticsStatusDialogVisible"
      width="40%"
      @close="showLogisticsStatusDialogClose"
    >
      <!--  -->
      <span>
        <el-alert
          title=""
          type="warning"
          center
          :closable="false"
          show-icon
        >
        </el-alert>
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in logisticInfo"
            :key="index"
            :timestamp="activity.time"
          >
            {{ activity.context }}
          </el-timeline-item>
        </el-timeline>
      </span>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="logisticsStatusDialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata.js'
export default {
  data () {
    return {
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 5
      },
      // 
      ordersList: [],
      // 
      total: 0,

      // 
      editAddressDialogVisible: false,
      editAddressrForm: {
        // /
        address1: [],
        // 
        address2: ''
      },
      editAddressrFormRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 5,
            max: 40,
            message: ' 5  40 ',
            trigger: 'blur'
          }
        ]
      },
      // 
      cityData: cityData,

      // 
      logisticsStatusDialogVisible: false,
      // 
      showLogisticsStatusrForm: {},
      // 1106975712662
      logisticId: '1106975712662',
      // logisticInfo 
      logisticInfo: [
        {
          time: '2018-05-10 09:39:00',
          ftime: '2018-05-10 09:39:00',
          context: ',,',
          location: ''
        },
        {
          time: '2018-05-10 08:23:00',
          ftime: '2018-05-10 08:23:00',
          context:
            '[]  95338',
          location: ''
        },
        {
          time: '2018-05-10 07:32:00',
          ftime: '2018-05-10 07:32:00',
          context: ' []',
          location: ''
        },
        {
          time: '2018-05-10 02:03:00',
          ftime: '2018-05-10 02:03:00',
          context:
            '[], []',
          location: ''
        },
        {
          time: '2018-05-09 23:05:00',
          ftime: '2018-05-09 23:05:00',
          context: ' []',
          location: ''
        },
        {
          time: '2018-05-09 21:21:00',
          ftime: '2018-05-09 21:21:00',
          context: '[], []',
          location: ''
        },
        {
          time: '2018-05-09 13:07:00',
          ftime: '2018-05-09 13:07:00',
          context: ' ',
          location: ''
        },
        {
          time: '2018-05-09 12:25:03',
          ftime: '2018-05-09 12:25:03',
          context: '',
          location: ''
        },
        {
          time: '2018-05-09 12:22:24',
          ftime: '2018-05-09 12:22:24',
          context: 'HLA',
          location: ''
        },
        {
          time: '2018-05-08 21:36:04',
          ftime: '2018-05-08 21:36:04',
          context: '',
          location: ''
        }
      ]
    }
  },
  created () {
    this.getOrdersList()
  },
  methods: {
    async getOrdersList () {
      // 
      this.queryInfo.query = this.queryInfo.query.trim()
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.ordersList = res.data.goods
      this.total = res.data.total
      //   console.log(res)
    },
    // pagesize
    handleSizeChange (newSize) {
      //   console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getOrdersList()
    },
    // 
    handleCurrentChange (newPage) {
      //   console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getOrdersList()
    },

    // 
    handleInputConfirm () {
      //   console.log(this.queryInfo)
      this.showTips()
    },
    // 
    showTips () {
      this.$message.warning('')
    },

    // 
    showEditGoodsAddressDialog () {
      this.editAddressDialogVisible = true
    },
    // 
    editAddressDialogClose () {
      this.$refs.editAddressrFormRef.resetFields()
    },
    // 
    submitEditAddress () {
      this.$refs.editAddressrFormRef.validate((valid) => {
        if (!valid) return
        // 
        this.editAddressDialogVisible = false
        this.$message.success('')
      })
    },

    // 
    showLogisticsStatusBox () {
      // 
      /* const { data: res } = await this.$http.post(
        'http://api.kdniao.cc/Ebusiness/EbusinessOrderHandle.aspx',
        {
          OrderCode: '',
          ShipperCode: 'SF',
          LogisticCode: '118650888018',
        }
      )
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg) */
      //   console.log(res)
      this.logisticsStatusDialogVisible = true
    },
    // 
    showLogisticsStatusDialogClose () {}
  }
}
</script>

<style lang="less" scoped>
@import '../../plugins/timeline/timeline.css';
@import '../../plugins/timeline-item/timeline-item.css';

.el-cascader {
  width: 100%;
}
.el-timeline {
  margin-top: 15px;
  font-size: 12px;
}
</style>
